module.exports = "<h1 id=popover>Popover</h1> <blockquote> <p>The Popover feature, which provides a tooltip-like behavior, can be easily applied to any interactive element via the <code>&lt;b-popover&gt;</code> component or <a href=/docs/directives/popover><code>v-b-popover</code></a> directive.</p> </blockquote> <pre class=hljs><span class=hljs-tag>&lt;<span class=hljs-name>div</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;text-center my-3&quot;</span>&gt;</span>\n  <span class=hljs-tag>&lt;<span class=hljs-name>b-btn</span> <span class=hljs-attr>v-b-popover.hover.auto</span>=<span class=hljs-string>&quot;&apos;I am popover content!&apos;&quot;</span> <span class=hljs-attr>title</span>=<span class=hljs-string>&quot;Popover Title&quot;</span>&gt;</span>Hover Me<span class=hljs-tag>&lt;/<span class=hljs-name>b-btn</span>&gt;</span>\n<span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n\n<span class=hljs-comment>&lt;!-- popover-example-1.vue --&gt;</span>\n</pre> <h2 id=overview>Overview</h2> <p>Things to know when using popover component:</p> <ul> <li>Popovers rely on the 3rd party library Popper.js for positioning. The library is bundled with Bootstrap-Vue dist files!</li> <li>Popovers with zero-length title <em>and</em> content are never displayed.</li> <li>Specify <code>container</code> as <code>null</code> (default, appends to <code>&lt;body&gt;</code>) to avoid rendering problems in more complex components (like input groups, button groups, etc). You can use <code>container</code> to optionally specify a different element to append the popover to.</li> <li>Triggering popovers on hidden elements will not work.</li> <li>Popovers for <code>disabled</code> elements must be triggered on a wrapper element.</li> <li>When triggered from hyperlinks that span multiple lines, popovers will be centered. Use <code>white-space: nowrap;</code> on your <code>&lt;a&gt;</code>s, <code>&lt;b-link&gt;</code>s and <code>&lt;router-link&gt;</code>s to avoid this behavior.</li> <li>Popovers must be hidden before their corresponding markup elements have been removed from the DOM.</li> </ul> <p>The <code>&lt;b-popover&gt;</code> component inserts a hidden (<code>display: none;</code>) <code>&lt;div&gt;</code> intermediate container element at the point in the DOM where the <code>&lt;b-popover&gt;</code> component is placed. This may affect layout and/or styling of components such as <code>&lt;b-button-group&gt;</code>, <code>&lt;b-button-toolbar&gt;</code>, and <code>&lt;b-input-group&gt;</code>. To avoid these posible layout issues, place the <code>&lt;b-popover&gt;</code> component <strong>outside</strong> of theese types of components.</p> <p>The target element <strong>must</strong> exist in the document before <code>&lt;b-popover&gt;</code> is mounted. If the target element is not found during mount, the popover will never open. Always place your <code>&lt;b-popover&gt;</code> component lower in the DOM than your target element.</p> <p><strong>Note:</strong> <em>When using slots for content and/or title, <code>&lt;b-popover&gt;</code> transfers the rendered DOM from those slots into the popover&apos;s markup when shown, and returns them back to the <code>&lt;b-popover&gt;</code> component when hidden. This may cause some issues in rare circumstances, so please test your implmentation accordingly! The <code>title</code> and <code>content</code> props do not have this behavior. For simple popovers, we recommend using the <code>v-b-popover</code> directive and enable the <code>html</code> modifer if needed.</em></p> <h2 id=positioning>Positioning</h2> <p>Twelve options are available for positioning: <code>top</code>, <code>topleft</code>, <code>topright</code>, <code>right</code>, <code>righttop</code>, <code>rightbottom</code>, <code>bottom</code>, <code>bottomleft</code>, <code>bottomright</code>, <code>left</code>, <code>lefttop</code>, and <code>leftbottom</code> aligned. Positioning is relative to the trigger element.</p> <div class=\"bd-example bd-example-popover-static\"> <div class=\"popover bs-popover-top bs-popover-top-docs\"> <div class=arrow></div> <h3 class=popover-header>Popover top</h3> <div class=popover-body> <p>Sed posuere consectetur est at lobortis. Aenean eu leo quam. Pellentesque ornare sem lacinia quam venenatis vestibulum.</p> </div> </div> <div class=\"popover bs-popover-top bs-popover-top-docs\"> <div class=arrow style=left:93%></div> <h3 class=popover-header>Popover topleft</h3> <div class=popover-body> <p>Sed posuere consectetur est at lobortis. Aenean eu leo quam. Pellentesque ornare sem lacinia quam venenatis vestibulum.</p> </div> </div> <div class=\"popover bs-popover-top bs-popover-top-docs\"> <div class=arrow style=left:4%></div> <h3 class=popover-header>Popover topright</h3> <div class=popover-body> <p>Sed posuere consectetur est at lobortis. Aenean eu leo quam. Pellentesque ornare sem lacinia quam venenatis vestibulum.</p> </div> </div> <div class=\"popover bs-popover-right bs-popover-right-docs\"> <div class=arrow></div> <h3 class=popover-header>Popover right</h3> <div class=popover-body> <p>Sed posuere consectetur est at lobortis. Aenean eu leo quam. Pellentesque ornare sem lacinia quam venenatis vestibulum.</p> </div> </div> <div class=\"popover bs-popover-right bs-popover-right-docs\"> <div class=arrow style=top:89%></div> <h3 class=popover-header>Popover righttop</h3> <div class=popover-body> <p>Sed posuere consectetur est at lobortis. Aenean eu leo quam. Pellentesque ornare sem lacinia quam venenatis vestibulum.</p> </div> </div> <div class=\"popover bs-popover-right bs-popover-right-docs\"> <div class=arrow style=top:7%></div> <h3 class=popover-header>Popover rightbottom</h3> <div class=popover-body> <p>Sed posuere consectetur est at lobortis. Aenean eu leo quam. Pellentesque ornare sem lacinia quam venenatis vestibulum.</p> </div> </div> <div class=\"popover bs-popover-bottom bs-popover-bottom-docs\"> <div class=arrow></div> <h3 class=popover-header>Popover bottom</h3> <div class=popover-body> <p>Sed posuere consectetur est at lobortis. Aenean eu leo quam. Pellentesque ornare sem lacinia quam venenatis vestibulum.</p> </div> </div> <div class=\"popover bs-popover-bottom bs-popover-bottom-docs\"> <div class=arrow style=left:93%></div> <h3 class=popover-header>Popover bottomleft</h3> <div class=popover-body> <p>Sed posuere consectetur est at lobortis. Aenean eu leo quam. Pellentesque ornare sem lacinia quam venenatis vestibulum.</p> </div> </div> <div class=\"popover bs-popover-bottom bs-popover-bottom-docs\"> <div class=arrow style=left:4%></div> <h3 class=popover-header>Popover bottomright</h3> <div class=popover-body> <p>Sed posuere consectetur est at lobortis. Aenean eu leo quam. Pellentesque ornare sem lacinia quam venenatis vestibulum.</p> </div> </div> <div class=\"popover bs-popover-left bs-popover-left-docs\"> <div class=arrow></div> <h3 class=popover-header>Popover left</h3> <div class=popover-body> <p>Sed posuere consectetur est at lobortis. Aenean eu leo quam. Pellentesque ornare sem lacinia quam venenatis vestibulum.</p> </div> </div> <div class=\"popover bs-popover-left bs-popover-left-docs\"> <div class=arrow style=top:89%></div> <h3 class=popover-header>Popover lefttop</h3> <div class=popover-body> <p>Sed posuere consectetur est at lobortis. Aenean eu leo quam. Pellentesque ornare sem lacinia quam venenatis vestibulum.</p> </div> </div> <div class=\"popover bs-popover-left bs-popover-left-docs\"> <div class=arrow style=top:7%></div> <h3 class=popover-header>Popover leftbottom</h3> <div class=popover-body> <p>Sed posuere consectetur est at lobortis. Aenean eu leo quam. Pellentesque ornare sem lacinia quam venenatis vestibulum.</p> </div> </div> <div class=clearfix></div> </div> <h2 id=triggers>Triggers</h2> <p>Popovers can be triggered (opened/closed) via any combination of <code>click</code>, <code>hover</code> and <code>focus</code>. The default trigger is <code>click</code>.</p> <p>If a popover has more than one trigger, then all triggers must be cleared before the popover will close. I.e. if a popover has the trigger <code>focus click</code>, and it was opened by <code>focus</code>, and the user then clicks the trigger element, they must click it again <strong>and</strong> move focus to close the popover.</p> <h3 id=dismiss-on-next-click-self-dimissing->Dismiss on next click (self dimissing)</h3> <p>Use the <code>focus</code> trigger by itself to dismiss popovers on the next click that the user makes. <code>focus</code> also makes the popover activate on both <code>focus</code> and <code>click</code> (as a click makes the element receive focus, assuming it is in the tab sequence of the page).</p> <p>You can, however, specify your trigger as <code>click blur</code>, which will make only a click activate the popover, and either a click on the element - <em>or losing foucus to another element or part of the document</em> - will close the popover.</p> <p>This <code>blur</code> trigger must be used in combination with the <code>click</code> trigger.</p> <h2 id=-b-popover-component-basic-usage><code>&lt;b-popover&gt;</code> Component basic usage</h2> <pre class=hljs><span class=hljs-tag>&lt;<span class=hljs-name>template</span>&gt;</span>\n  <span class=hljs-tag>&lt;<span class=hljs-name>b-container</span> <span class=hljs-attr>fluid</span>&gt;</span>\n\n    <span class=hljs-tag>&lt;<span class=hljs-name>h5</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;my-3&quot;</span>&gt;</span>Placement<span class=hljs-tag>&lt;/<span class=hljs-name>h5</span>&gt;</span>\n    <span class=hljs-tag>&lt;<span class=hljs-name>b-row</span>&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>b-col</span> <span class=hljs-attr>md</span>=<span class=hljs-string>&quot;4&quot;</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;py-4 text-center&quot;</span>\n           <span class=hljs-attr>v-for</span>=<span class=hljs-string>&quot;placement in placements&quot;</span> <span class=hljs-attr>:key</span>=<span class=hljs-string>&quot;placement&quot;</span>&gt;</span>\n        <span class=hljs-tag>&lt;<span class=hljs-name>b-btn</span> <span class=hljs-attr>:id</span>=<span class=hljs-string>&quot;&apos;exPopover1-&apos;+placement&quot;</span> <span class=hljs-attr>variant</span>=<span class=hljs-string>&quot;primary&quot;</span>&gt;</span>\n          {{ placement }}\n        <span class=hljs-tag>&lt;/<span class=hljs-name>b-btn</span>&gt;</span>\n        <span class=hljs-tag>&lt;<span class=hljs-name>b-popover</span> <span class=hljs-attr>:target</span>=<span class=hljs-string>&quot;&apos;exPopover1-&apos;+placement&quot;</span>\n                   <span class=hljs-attr>:placement</span>=<span class=hljs-string>&quot;placement&quot;</span>\n                   <span class=hljs-attr>title</span>=<span class=hljs-string>&quot;Popover!&quot;</span>\n                   <span class=hljs-attr>triggers</span>=<span class=hljs-string>&quot;hover focus&quot;</span>\n                   <span class=hljs-attr>:content</span>=<span class=hljs-string>&quot;`Placement ${placement}`&quot;</span>&gt;</span>\n        <span class=hljs-tag>&lt;/<span class=hljs-name>b-popover</span>&gt;</span>\n      <span class=hljs-tag>&lt;/<span class=hljs-name>b-col</span>&gt;</span>\n    <span class=hljs-tag>&lt;/<span class=hljs-name>b-row</span>&gt;</span>\n\n    <span class=hljs-tag>&lt;<span class=hljs-name>h5</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;my-3&quot;</span>&gt;</span>Content via properties or slots<span class=hljs-tag>&lt;/<span class=hljs-name>h5</span>&gt;</span>\n    <span class=hljs-tag>&lt;<span class=hljs-name>b-row</span>&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>b-col</span> <span class=hljs-attr>md</span>=<span class=hljs-string>&quot;6&quot;</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;py-4 text-center&quot;</span>&gt;</span>\n        <span class=hljs-tag>&lt;<span class=hljs-name>b-btn</span> <span class=hljs-attr>id</span>=<span class=hljs-string>&quot;exPopover2&quot;</span> <span class=hljs-attr>variant</span>=<span class=hljs-string>&quot;primary&quot;</span>&gt;</span>Using properties<span class=hljs-tag>&lt;/<span class=hljs-name>b-btn</span>&gt;</span>\n        <span class=hljs-tag>&lt;<span class=hljs-name>b-popover</span> <span class=hljs-attr>target</span>=<span class=hljs-string>&quot;exPopover2&quot;</span>\n            <span class=hljs-attr>title</span>=<span class=hljs-string>&quot;Prop Examples&quot;</span>\n            <span class=hljs-attr>triggers</span>=<span class=hljs-string>&quot;hover focus&quot;</span>\n            <span class=hljs-attr>content</span>=<span class=hljs-string>&quot;Embedding content using properties is easy&quot;</span>&gt;</span>\n        <span class=hljs-tag>&lt;/<span class=hljs-name>b-popover</span>&gt;</span>\n      <span class=hljs-tag>&lt;/<span class=hljs-name>b-col</span>&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>b-col</span> <span class=hljs-attr>md</span>=<span class=hljs-string>&quot;6&quot;</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;py-4 text-center&quot;</span>&gt;</span>\n        <span class=hljs-tag>&lt;<span class=hljs-name>b-btn</span> <span class=hljs-attr>id</span>=<span class=hljs-string>&quot;exPopover3&quot;</span> <span class=hljs-attr>variant</span>=<span class=hljs-string>&quot;primary&quot;</span>&gt;</span>Using slots<span class=hljs-tag>&lt;/<span class=hljs-name>b-btn</span>&gt;</span>\n        <span class=hljs-tag>&lt;<span class=hljs-name>b-popover</span> <span class=hljs-attr>target</span>=<span class=hljs-string>&quot;exPopover3&quot;</span> <span class=hljs-attr>triggers</span>=<span class=hljs-string>&quot;hover focus&quot;</span>&gt;</span>\n           <span class=hljs-tag>&lt;<span class=hljs-name>template</span> <span class=hljs-attr>slot</span>=<span class=hljs-string>&quot;title&quot;</span>&gt;</span>Content via Slots<span class=hljs-tag>&lt;/<span class=hljs-name>template</span>&gt;</span>\n           Embedding content <span class=hljs-tag>&lt;<span class=hljs-name>span</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;text-danger&quot;</span>&gt;</span>using slots<span class=hljs-tag>&lt;/<span class=hljs-name>span</span>&gt;</span>\n           affords you <span class=hljs-tag>&lt;<span class=hljs-name>em</span>&gt;</span>greater <span class=hljs-tag>&lt;<span class=hljs-name>strong</span>&gt;</span>control.<span class=hljs-tag>&lt;/<span class=hljs-name>strong</span>&gt;</span><span class=hljs-tag>&lt;/<span class=hljs-name>em</span>&gt;</span> and\n           basic HTML support.\n        <span class=hljs-tag>&lt;/<span class=hljs-name>b-popover</span>&gt;</span>\n      <span class=hljs-tag>&lt;/<span class=hljs-name>b-col</span>&gt;</span>\n    <span class=hljs-tag>&lt;/<span class=hljs-name>b-row</span>&gt;</span>\n\n  <span class=hljs-tag>&lt;/<span class=hljs-name>b-container</span>&gt;</span>\n<span class=hljs-tag>&lt;/<span class=hljs-name>template</span>&gt;</span>\n\n<span class=hljs-tag>&lt;<span class=hljs-name>script</span>&gt;</span><span class=javascript>\n<span class=hljs-keyword>export</span> <span class=hljs-keyword>default</span> {\n    <span class=hljs-attr>data</span>: {\n        <span class=hljs-attr>placements</span>: [\n            <span class=hljs-string>&apos;topright&apos;</span>, <span class=hljs-string>&apos;top&apos;</span>, <span class=hljs-string>&apos;topleft&apos;</span>,\n            <span class=hljs-string>&apos;bottomright&apos;</span>, <span class=hljs-string>&apos;bottom&apos;</span>, <span class=hljs-string>&apos;bottomleft&apos;</span>,\n            <span class=hljs-string>&apos;righttop&apos;</span>, <span class=hljs-string>&apos;right&apos;</span>, <span class=hljs-string>&apos;lefttop&apos;</span>,\n            <span class=hljs-string>&apos;rightbottom&apos;</span>, <span class=hljs-string>&apos;left&apos;</span>, <span class=hljs-string>&apos;leftbottom&apos;</span>\n        ]\n    }\n}\n</span><span class=hljs-tag>&lt;/<span class=hljs-name>script</span>&gt;</span>\n\n<span class=hljs-comment>&lt;!-- popover-1.vue --&gt;</span>\n</pre> <h3 id=component-options-via-props>Component options via props</h3> <table class=\"table b-table table-sm table-striped\"> <thead class=thead-default> <tr> <th>Prop</th> <th>Default</th> <th>Description</th> <th>Supported values</th> </tr> </thead> <tbody> <tr> <td><code>target</code></td> <td><code>null</code></td> <td>String ID of element, or a reference to an element or component, that you want to trigger the popover. <strong>Required</strong></td> <td>Any valid, in-document unique element ID, or in-document element/component reference</td> </tr> <tr> <td><code>title</code></td> <td><code>null</code></td> <td>Title of popover (text only, no HTML). if HTML is required, place it in the <code>title</code> named slot</td> <td>Plain text</td> </tr> <tr> <td><code>content</code></td> <td><code>null</code></td> <td>Content of popover (text only, no HTML). if HTML is required, place it in the default slot</td> <td>Plain text</td> </tr> <tr> <td><code>placement</code></td> <td><code>&apos;top&apos;</code></td> <td>Positioning of the popover, relative to the trigger element.</td> <td><code>top</code>, <code>bottom</code>, <code>left</code>, <code>right</code>, <code>auto</code>, <code>topleft</code>, <code>topright</code>, <code>bottomleft</code>, <code>bottomright</code>, <code>lefttop</code>, <code>leftbottom</code>, <code>righttop</code>, <code>rightbottom</code></td> </tr> <tr> <td><code>triggers</code></td> <td><code>&apos;click&apos;</code></td> <td>Space separated list of which event(s) will trigger open/close of popover</td> <td><code>hover</code>, <code>focus</code>, <code>click</code>. Note <code>blur</code> is a special use case to close popover on next click.</td> </tr> <tr> <td><code>no-fade</code></td> <td><code>false</code></td> <td>Disable fade animation when set to <code>true</code></td> <td><code>true</code> or <code>false</code></td> </tr> <tr> <td><code>delay</code></td> <td><code>0</code></td> <td>Number of milliseconds to delay showing and hidding of popover. Can also be specified as an object in the form of <code>{ show: 100, hide: 400 }</code> allowing different show and hide delays</td> <td><code>0</code> and up, integers only.</td> </tr> <tr> <td><code>offset</code></td> <td><code>0</code></td> <td>Number of pixels to shift the center of the popover. Also affects the position of the popover arrow.</td> <td>Any negative or positive integer</td> </tr> <tr> <td><code>container</code></td> <td><code>null</code></td> <td>String ID of element to append rendered popover into. If <code>null</code> or element not found, popover is appended to <code>&lt;body&gt;</code> (default)</td> <td>Any valid in-document unique element ID.</td> </tr> </tbody> </table> <h2 id=-v-b-popover-directive-usage><code>v-b-popover</code> Directive usage</h2> <p>Just need quick popovers without too much markup? Use the <a href=/docs/directives/popover><code>v-b-popover</code> directive</a>:</p> <pre class=hljs><span class=hljs-tag>&lt;<span class=hljs-name>template</span>&gt;</span>\n    <span class=hljs-tag>&lt;<span class=hljs-name>b-container</span> <span class=hljs-attr>fluid</span>&gt;</span>\n\n      <span class=hljs-tag>&lt;<span class=hljs-name>h4</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;mt-sm-4 ms-sm-4 text-muted&quot;</span>&gt;</span>Placement<span class=hljs-tag>&lt;/<span class=hljs-name>h4</span>&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>b-row</span>&gt;</span>\n        <span class=hljs-tag>&lt;<span class=hljs-name>b-col</span> <span class=hljs-attr>md</span>=<span class=hljs-string>&quot;3&quot;</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;py-3 text-center&quot;</span>&gt;</span>\n          <span class=hljs-tag>&lt;<span class=hljs-name>b-btn</span> <span class=hljs-attr>v-b-popover.hover.top</span>=<span class=hljs-string>&quot;&apos;I am Top&apos;&quot;</span>\n                 <span class=hljs-attr>title</span>=<span class=hljs-string>&quot;Popover!&quot;</span>\n                 <span class=hljs-attr>variant</span>=<span class=hljs-string>&quot;primary&quot;</span>&gt;</span>Top<span class=hljs-tag>&lt;/<span class=hljs-name>b-btn</span>&gt;</span>\n        <span class=hljs-tag>&lt;/<span class=hljs-name>b-col</span>&gt;</span>\n        <span class=hljs-tag>&lt;<span class=hljs-name>b-col</span> <span class=hljs-attr>md</span>=<span class=hljs-string>&quot;3&quot;</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;py-3 text-center&quot;</span>&gt;</span>\n          <span class=hljs-tag>&lt;<span class=hljs-name>b-btn</span> <span class=hljs-attr>v-b-popover.hover.left</span>=<span class=hljs-string>&quot;&apos;I am Left&apos;&quot;</span>\n                 <span class=hljs-attr>title</span>=<span class=hljs-string>&quot;Popover!&quot;</span>\n                 <span class=hljs-attr>variant</span>=<span class=hljs-string>&quot;primary&quot;</span>&gt;</span>Left<span class=hljs-tag>&lt;/<span class=hljs-name>b-btn</span>&gt;</span>\n        <span class=hljs-tag>&lt;/<span class=hljs-name>b-col</span>&gt;</span>\n        <span class=hljs-tag>&lt;<span class=hljs-name>b-col</span> <span class=hljs-attr>md</span>=<span class=hljs-string>&quot;3&quot;</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;py-3 text-center&quot;</span>&gt;</span>\n          <span class=hljs-tag>&lt;<span class=hljs-name>b-btn</span> <span class=hljs-attr>v-b-popover.hover.right</span>=<span class=hljs-string>&quot;&apos;I am Right&apos;&quot;</span>\n                 <span class=hljs-attr>title</span>=<span class=hljs-string>&quot;Popover!&quot;</span>\n                 <span class=hljs-attr>variant</span>=<span class=hljs-string>&quot;primary&quot;</span>&gt;</span>Right<span class=hljs-tag>&lt;/<span class=hljs-name>b-btn</span>&gt;</span>\n        <span class=hljs-tag>&lt;/<span class=hljs-name>b-col</span>&gt;</span>\n        <span class=hljs-tag>&lt;<span class=hljs-name>b-col</span> <span class=hljs-attr>md</span>=<span class=hljs-string>&quot;3&quot;</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;py-3 text-center&quot;</span>&gt;</span>\n          <span class=hljs-tag>&lt;<span class=hljs-name>b-btn</span> <span class=hljs-attr>v-b-popover.hover.bottom</span>=<span class=hljs-string>&quot;&apos;I am Bottom&apos;&quot;</span>\n                 <span class=hljs-attr>title</span>=<span class=hljs-string>&quot;Popover!&quot;</span>\n                 <span class=hljs-attr>variant</span>=<span class=hljs-string>&quot;primary&quot;</span>&gt;</span>Bottom<span class=hljs-tag>&lt;/<span class=hljs-name>b-btn</span>&gt;</span>\n        <span class=hljs-tag>&lt;/<span class=hljs-name>b-col</span>&gt;</span>\n      <span class=hljs-tag>&lt;/<span class=hljs-name>b-row</span>&gt;</span>\n\n    <span class=hljs-tag>&lt;/<span class=hljs-name>b-container</span>&gt;</span>\n<span class=hljs-tag>&lt;/<span class=hljs-name>template</span>&gt;</span>\n\n<span class=hljs-comment>&lt;!-- popover-directive-1.vue --&gt;</span>\n</pre> <p>Refer to the <a href=/docs/directives/popover><code>v-b-popover</code> directive</a> documentation for detailed information on the directive usage.</p> <h2 id=advanced-b-popover-usage-with-reactive-content>Advanced <code>&lt;b-popover&gt;</code> usage with reactive content</h2> <p>You can even make your <code>&lt;b-popover&gt;</code> content interactive. Just remember not to use the <code>focus</code>, <code>hover</code> or <code>blur</code> triggers (use only <code>click</code>), otherwsie your popover will close automatically as soon as someone trys to interact with the content.</p> <p>If you absolutely must use a trigger other than <code>click</code> (or want to disable closing of the popover when the trigger element is clicked a second time), then you can either:</p> <ul> <li>Listen for the <code>hide</code> event on the <code>&lt;b-popover&gt;</code> element, and call the <code>preventDefault()</code> method (when apropriate) on the <code>BvEvent</code> passed to your <code>hide</code> handler, or</li> <li>Disable your trigger element (if possible) as soon as the popover opens (via the <code>show</code> event), and re-enable it when apropriate.</li> </ul> <p>For practical purposes, interactive content popovers should be minimal. The maximum width of the popover is hard coded by Bootstrap V4 CSS to <code>276px</code>. Tall popovers on small screens can be harder to deal with on mobile devices (such as smart-phones).</p> <pre class=hljs><span class=hljs-tag>&lt;<span class=hljs-name>template</span>&gt;</span>\n  <span class=hljs-tag>&lt;<span class=hljs-name>div</span> <span class=hljs-attr>id</span>=<span class=hljs-string>&quot;myContainer&quot;</span>&gt;</span>\n    <span class=hljs-tag>&lt;<span class=hljs-name>div</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;my-3&quot;</span>&gt;</span>\n      <span class=hljs-comment>&lt;!-- our triggering (target) element --&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>b-btn</span> <span class=hljs-attr>id</span>=<span class=hljs-string>&quot;exPopoverReactive1&quot;</span>\n             <span class=hljs-attr>:disabled</span>=<span class=hljs-string>&quot;disabled&quot;</span>\n             <span class=hljs-attr>variant</span>=<span class=hljs-string>&quot;primary&quot;</span>\n             <span class=hljs-attr>ref</span>=<span class=hljs-string>&quot;button&quot;</span>&gt;</span>\n        Reactive Content Using Slots\n      <span class=hljs-tag>&lt;/<span class=hljs-name>b-btn</span>&gt;</span>\n    <span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n\n    <span class=hljs-comment>&lt;!-- output from the popover interaction --&gt;</span>\n    <span class=hljs-tag>&lt;<span class=hljs-name>b-card</span> <span class=hljs-attr>title</span>=<span class=hljs-string>&quot;Returned values:&quot;</span> <span class=hljs-attr>v-if</span>=<span class=hljs-string>&quot;input1Return &amp;&amp; input2Return&quot;</span>&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>p</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;card-text&quot;</span> <span class=hljs-attr>style</span>=<span class=hljs-string>&quot;max-width:20rem;&quot;</span>&gt;</span>\n        Name: <span class=hljs-tag>&lt;<span class=hljs-name>strong</span>&gt;</span>{{ input1Return }}<span class=hljs-tag>&lt;/<span class=hljs-name>strong</span>&gt;</span><span class=hljs-tag>&lt;<span class=hljs-name>br</span>&gt;</span>\n        Color: <span class=hljs-tag>&lt;<span class=hljs-name>strong</span>&gt;</span>{{ input2Return }}<span class=hljs-tag>&lt;/<span class=hljs-name>strong</span>&gt;</span>\n      <span class=hljs-tag>&lt;/<span class=hljs-name>p</span>&gt;</span>\n    <span class=hljs-tag>&lt;/<span class=hljs-name>b-card</span>&gt;</span>\n\n    <span class=hljs-comment>&lt;!-- Our popover title and content render container --&gt;</span>\n    <span class=hljs-comment>&lt;!-- We use placement &apos;auto&apos; so popover fits in the best spot on viewport --&gt;</span>\n    <span class=hljs-comment>&lt;!-- We specify the same container as the trigger button, so that popover is close to button in tab sequence --&gt;</span>\n    <span class=hljs-tag>&lt;<span class=hljs-name>b-popover</span> <span class=hljs-attr>target</span>=<span class=hljs-string>&quot;exPopoverReactive1&quot;</span>\n               <span class=hljs-attr>triggers</span>=<span class=hljs-string>&quot;click&quot;</span>\n               <span class=hljs-attr>placement</span>=<span class=hljs-string>&quot;auto&quot;</span>\n               <span class=hljs-attr>container</span>=<span class=hljs-string>&quot;myContainer&quot;</span>\n               <span class=hljs-attr>ref</span>=<span class=hljs-string>&quot;popover&quot;</span>\n               @<span class=hljs-attr>show</span>=<span class=hljs-string>&quot;onShow&quot;</span>\n               @<span class=hljs-attr>shown</span>=<span class=hljs-string>&quot;onShown&quot;</span>\n               @<span class=hljs-attr>hidden</span>=<span class=hljs-string>&quot;onHidden&quot;</span>&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>template</span> <span class=hljs-attr>slot</span>=<span class=hljs-string>&quot;title&quot;</span>&gt;</span>\n        <span class=hljs-tag>&lt;<span class=hljs-name>b-btn</span> @<span class=hljs-attr>click</span>=<span class=hljs-string>&quot;onClose&quot;</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;close&quot;</span> <span class=hljs-attr>aria-label</span>=<span class=hljs-string>&quot;Close&quot;</span>&gt;</span>\n          <span class=hljs-tag>&lt;<span class=hljs-name>span</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;d-inline-block&quot;</span> <span class=hljs-attr>aria-hidden</span>=<span class=hljs-string>&quot;true&quot;</span>&gt;</span>&amp;times;<span class=hljs-tag>&lt;/<span class=hljs-name>span</span>&gt;</span>\n        <span class=hljs-tag>&lt;/<span class=hljs-name>b-btn</span>&gt;</span>\n        Interactive Content\n      <span class=hljs-tag>&lt;/<span class=hljs-name>template</span>&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>div</span>&gt;</span>\n        <span class=hljs-tag>&lt;<span class=hljs-name>b-form-group</span> <span class=hljs-attr>label</span>=<span class=hljs-string>&quot;Name&quot;</span> <span class=hljs-attr>:state</span>=<span class=hljs-string>&quot;input1state&quot;</span> <span class=hljs-attr>horizontal</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;mb-1&quot;</span>\n           <span class=hljs-attr>description</span>=<span class=hljs-string>&quot;Enter your name&quot;</span> <span class=hljs-attr>feedback</span>=<span class=hljs-string>&quot;This field is required&quot;</span>&gt;</span>\n          <span class=hljs-tag>&lt;<span class=hljs-name>b-form-input</span> <span class=hljs-attr>ref</span>=<span class=hljs-string>&quot;input1&quot;</span> <span class=hljs-attr>:state</span>=<span class=hljs-string>&quot;input1state&quot;</span> <span class=hljs-attr>size</span>=<span class=hljs-string>&quot;sm&quot;</span> <span class=hljs-attr>v-model</span>=<span class=hljs-string>&quot;input1&quot;</span>&gt;</span><span class=hljs-tag>&lt;/<span class=hljs-name>b-form-input</span>&gt;</span>\n        <span class=hljs-tag>&lt;/<span class=hljs-name>b-form-group</span>&gt;</span>\n        <span class=hljs-tag>&lt;<span class=hljs-name>b-form-group</span> <span class=hljs-attr>label</span>=<span class=hljs-string>&quot;Color&quot;</span> <span class=hljs-attr>:state</span>=<span class=hljs-string>&quot;input2state&quot;</span> <span class=hljs-attr>horizontal</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;mb-1&quot;</span>\n           <span class=hljs-attr>description</span>=<span class=hljs-string>&quot;Pick a color&quot;</span> <span class=hljs-attr>feedback</span>=<span class=hljs-string>&quot;This field is required&quot;</span>&gt;</span>\n          <span class=hljs-tag>&lt;<span class=hljs-name>b-form-select</span> <span class=hljs-attr>size</span>=<span class=hljs-string>&quot;sm&quot;</span> <span class=hljs-attr>:state</span>=<span class=hljs-string>&quot;input2state&quot;</span> <span class=hljs-attr>v-model</span>=<span class=hljs-string>&quot;input2&quot;</span> <span class=hljs-attr>:options</span>=<span class=hljs-string>&quot;options&quot;</span>&gt;</span><span class=hljs-tag>&lt;/<span class=hljs-name>b-form-select</span>&gt;</span>\n        <span class=hljs-tag>&lt;/<span class=hljs-name>b-form-group</span>&gt;</span>\n        <span class=hljs-tag>&lt;<span class=hljs-name>b-alert</span> <span class=hljs-attr>show</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;small&quot;</span>&gt;</span>\n          <span class=hljs-tag>&lt;<span class=hljs-name>strong</span>&gt;</span>Current Values:<span class=hljs-tag>&lt;/<span class=hljs-name>strong</span>&gt;</span><span class=hljs-tag>&lt;<span class=hljs-name>br</span>&gt;</span>\n          Name: <span class=hljs-tag>&lt;<span class=hljs-name>strong</span>&gt;</span>{{ input1 }}<span class=hljs-tag>&lt;/<span class=hljs-name>strong</span>&gt;</span><span class=hljs-tag>&lt;<span class=hljs-name>br</span>&gt;</span>\n          Color: <span class=hljs-tag>&lt;<span class=hljs-name>strong</span>&gt;</span>{{ input2 }}<span class=hljs-tag>&lt;/<span class=hljs-name>strong</span>&gt;</span>\n        <span class=hljs-tag>&lt;/<span class=hljs-name>b-alert</span>&gt;</span>\n        <span class=hljs-tag>&lt;<span class=hljs-name>b-btn</span> @<span class=hljs-attr>click</span>=<span class=hljs-string>&quot;onCancel&quot;</span> <span class=hljs-attr>size</span>=<span class=hljs-string>&quot;sm&quot;</span> <span class=hljs-attr>variant</span>=<span class=hljs-string>&quot;danger&quot;</span>&gt;</span>Cancel<span class=hljs-tag>&lt;/<span class=hljs-name>b-btn</span>&gt;</span>\n        <span class=hljs-tag>&lt;<span class=hljs-name>b-btn</span> @<span class=hljs-attr>click</span>=<span class=hljs-string>&quot;onOk&quot;</span> <span class=hljs-attr>size</span>=<span class=hljs-string>&quot;sm&quot;</span> <span class=hljs-attr>variant</span>=<span class=hljs-string>&quot;primary&quot;</span>&gt;</span>Ok<span class=hljs-tag>&lt;/<span class=hljs-name>b-btn</span>&gt;</span>\n      <span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n    <span class=hljs-tag>&lt;/<span class=hljs-name>b-popover</span>&gt;</span>\n  <span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n<span class=hljs-tag>&lt;/<span class=hljs-name>template</span>&gt;</span>\n\n<span class=hljs-tag>&lt;<span class=hljs-name>script</span>&gt;</span><span class=javascript>\n  <span class=hljs-keyword>export</span> <span class=hljs-keyword>default</span> {\n    <span class=hljs-attr>data</span>: {\n      <span class=hljs-attr>input1</span>: <span class=hljs-string>&apos;&apos;</span>,\n      <span class=hljs-attr>input1state</span>: <span class=hljs-literal>null</span>,\n      <span class=hljs-attr>input2</span>: <span class=hljs-string>&apos;&apos;</span>,\n      <span class=hljs-attr>input2state</span>: <span class=hljs-literal>null</span>,\n      <span class=hljs-attr>options</span>: [{<span class=hljs-attr>text</span>:<span class=hljs-string>&apos;- Choose 1 -&apos;</span>, <span class=hljs-attr>value</span>:<span class=hljs-string>&apos;&apos;</span>},<span class=hljs-string>&apos;Red&apos;</span>,<span class=hljs-string>&apos;Green&apos;</span>,<span class=hljs-string>&apos;Blue&apos;</span>],\n      <span class=hljs-attr>input1Return</span>: <span class=hljs-string>&apos;&apos;</span>,\n      <span class=hljs-attr>input2Return</span>: <span class=hljs-string>&apos;&apos;</span>,\n      <span class=hljs-attr>disabled</span>: <span class=hljs-literal>false</span>\n    },\n    <span class=hljs-attr>watch</span>: {\n      input1(val) {\n        <span class=hljs-keyword>if</span> (val) {\n          <span class=hljs-keyword>this</span>.input1state = <span class=hljs-literal>true</span>\n        }\n      },\n      input2(val) {\n        <span class=hljs-keyword>if</span> (val) {\n          <span class=hljs-keyword>this</span>.input2state = <span class=hljs-literal>true</span>\n        }\n      },\n    },\n    <span class=hljs-attr>methods</span>: {\n      onClose() {\n        <span class=hljs-comment>// Emitting &apos;close&apos; on the popover will trigger it to hide for us</span>\n        <span class=hljs-keyword>this</span>.$refs.popover.$emit(<span class=hljs-string>&apos;close&apos;</span>);\n      },\n      onCancel() {\n        <span class=hljs-comment>// Emitting &apos;close&apos; on the popover will trigger it to hide for us</span>\n        <span class=hljs-keyword>this</span>.$refs.popover.$emit(<span class=hljs-string>&apos;close&apos;</span>);\n      },\n      onOk() {\n       <span class=hljs-keyword>if</span> (!<span class=hljs-keyword>this</span>.input1) { <span class=hljs-keyword>this</span>.input1state=<span class=hljs-literal>false</span> }\n       <span class=hljs-keyword>if</span> (!<span class=hljs-keyword>this</span>.input2) { <span class=hljs-keyword>this</span>.input2state=<span class=hljs-literal>false</span> }\n       <span class=hljs-keyword>if</span> (<span class=hljs-keyword>this</span>.input1 &amp;&amp; <span class=hljs-keyword>this</span>.input2) {\n          <span class=hljs-comment>// Emitting &apos;close&apos; on the popover will trigger it to hide for us</span>\n          <span class=hljs-keyword>this</span>.$refs.popover.$emit(<span class=hljs-string>&apos;close&apos;</span>);\n          <span class=hljs-comment>// &quot;Return&quot; our popover &quot;form&quot; results</span>\n          <span class=hljs-keyword>this</span>.input1Return = <span class=hljs-keyword>this</span>.input1;\n          <span class=hljs-keyword>this</span>.input2Return = <span class=hljs-keyword>this</span>.input2;\n        }\n      },\n      onShow() {\n        <span class=hljs-comment>// This is called just before the popover is shown</span>\n        <span class=hljs-comment>// Reset our popover &quot;form&quot; variables</span>\n        <span class=hljs-keyword>this</span>.input1 = <span class=hljs-string>&apos;&apos;</span>;\n        <span class=hljs-keyword>this</span>.input2 = <span class=hljs-string>&apos;&apos;</span>;\n        <span class=hljs-keyword>this</span>.input1state = <span class=hljs-literal>null</span>;\n        <span class=hljs-keyword>this</span>.input2state = <span class=hljs-literal>null</span>;\n        <span class=hljs-keyword>this</span>.input1Return = <span class=hljs-string>&apos;&apos;</span>;\n        <span class=hljs-keyword>this</span>.input2Return = <span class=hljs-string>&apos;&apos;</span>;\n        <span class=hljs-comment>// Disable our trigger button to prevent popover closing on second click</span>\n        <span class=hljs-keyword>this</span>.disabled = <span class=hljs-literal>true</span>;\n      },\n      onShown() {\n        <span class=hljs-comment>// Called just after the popover has been shown</span>\n        <span class=hljs-comment>// Transfer focus to the first input</span>\n        <span class=hljs-keyword>this</span>.focusRef(<span class=hljs-keyword>this</span>.$refs.input1);\n      },\n      onHidden() {\n        <span class=hljs-comment>// Called just after the popover has finished hiding</span>\n        <span class=hljs-comment>// We re-enable our button</span>\n        <span class=hljs-keyword>this</span>.disabled = <span class=hljs-literal>false</span>;\n        <span class=hljs-comment>// And bring focus back to it</span>\n        <span class=hljs-keyword>this</span>.focusRef(<span class=hljs-keyword>this</span>.$refs.button);\n      },\n      focusRef(ref) {\n        <span class=hljs-comment>// Some references may be a component, functional component, or plain element</span>\n        <span class=hljs-comment>// This handles that check before focusing, assuming a focus() method exists</span>\n        <span class=hljs-comment>// We do this in a double nextTick to ensure components have updated &amp; popover positioned first</span>\n        <span class=hljs-keyword>this</span>.$nextTick(<span class=hljs-function><span class=hljs-params>()</span> =&gt;</span> {\n          <span class=hljs-keyword>this</span>.$nextTick(<span class=hljs-function><span class=hljs-params>()</span> =&gt;</span> { (ref.$el || ref).focus(); });\n        });\n      }\n    }\n  };\n</span><span class=hljs-tag>&lt;/<span class=hljs-name>script</span>&gt;</span>\n\n<span class=hljs-comment>&lt;!-- popover-advanced-1.vue --&gt;</span>\n</pre> <h2 id=closing-popovers>Closing popovers</h2> <p>You can close all open popovers by emitting the <code>bv::hide::popover</code> event on $root:</p> <pre class=hljs><span class=hljs-keyword>this</span>.$root.$emit(<span class=hljs-string>&apos;bv::hide::popover&apos;</span>);\n</pre> <h2 id=accessibility>Accessibility</h2> <p>Popovers, in their current state, are not overly accessible when used as interactive components. Content may not be activly read to screen reader users, and the popover markup not be located close to the trigger element in the DOM (as popovers usually get appended to the end of <code>&lt;body&gt;</code>).</p> <p>When using popovers as interactive component, you should transfer focus into the popover if possible. When the popover is closed, you should return focus back to your triggering element (assuming <code>focus</code> is not used as a trigger method), as we have done in the above example.</p> <p>You may also want to implement focus containment in the popover content while the user is interactiving with it (keeping focus inside the popover until it is closed by the user).</p> <h2 id=component-reference>Component Reference</h2> ";

